<template>
  <v-app-bar app dense elevation="0">
    <div class="d-flex align-center">
      <h4>Lalala Uye</h4>
    </div>

    <v-spacer></v-spacer>
    <v-switch v-model="darkMode" hide-details></v-switch>
    <div class="d-flex" style="border: 1px solid;">
      <v-sheet
        v-for="color in colorPallete"
        :key="color"
        :color="color"
        height="32"
        width="32"
      ></v-sheet>
    </div>
  </v-app-bar>
</template>

<script>
export default {
  data() {
    return {
      colorPallete: [
        "secondary lighten-1",
        "primary lighten-1",
        "primary",
        "primary darken-1",
        "secondary",
      ],
      darkMode: true,
    };
  },
  watch: {
    darkMode(newVal, oldVal) {
      // if (this.darkMode) {
      //   this.$vuetify.theme.dark = true
      // } else {
      //   this.$vuetify.theme.dark = false
      // }
      this.handleDarkMode();
    }
  },
  methods: {
    handleDarkMode () {
      // if (process.browser) {
        if (this.darkMode === true) {
          this.$vuetify.theme.dark = true
          localStorage.setItem('DarkMode', true)
        } else {
          this.$vuetify.theme.dark = false
          localStorage.setItem('DarkMode', false)
        }
      // }
    }
  },
  created () {
    // if (process.browser) {
      if (localStorage.getItem('DarkMode')) {
        const cookieValue = localStorage.getItem('DarkMode') === 'true'
        this.darkMode = cookieValue
      } else {
        this.handleDarkMode()
      }
    // }
  },
};
</script>

<style lang="scss">
.v-toolbar { 
  &.theme--light {
    .v-toolbar__content {
      border-bottom: 1px solid rgba(0, 0, 0, 0.12);
    }
  }
  &.theme--dark {
    .v-toolbar__content {
      border-bottom: 1px solid rgba(255, 255, 255, 0.12);
    }
  }
}
</style>