<template>
  <div class="d-flex justify-center align-center" style="height: 70vmin;">
    <div class="game-wrapper d-flex flex-column">
      <div class="game-details d-flex justify-space-between pa-2">
        <span>Score : {{ score }}</span>
        <span>High Score : {{ highScore }}</span>
      </div>
      <div class="game-play-board w-full h-full">

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      foodX: null,
      foodY: null,
      snakeX: 10,
      snakeY: 10,
      snakeBody: [],
      velocityX: null,
      velocityY: null,
      gameOver: false,
      intervalId: undefined,
      score: 0,
      highScore: localStorage.getItem("high-score") || 0,
    }
  },
  methods: {
    initGame() {
      if (this.gameOver) {
        this.handleGameOver()
      }
      const playBoard = document.querySelector('.game-play-board')
      let htmlMarkup = `<div class="food" style="grid-area: ${this.foodY} / ${this.foodX}"></div>`

      if(this.snakeX === this.foodX && this.snakeY === this.foodY) {
        this.randomFood()
        this.snakeBody.push([this.foodX, this.foodY])
        this.score += 5

        const newHighScore = this.score >= this.highScore ? this.score : this.highScore
        localStorage.setItem("high-score", newHighScore)
      }

      for (let i = this.snakeBody.length - 1; i > 0; i--) {
        this.snakeBody[i] = this.snakeBody[i - 1]
      }
      this.snakeBody[0] = [this.snakeX, this.snakeY]

      this.snakeX += this.velocityX
      this.snakeY += this.velocityY

      if (this.snakeX <= 0 || this.snakeX > 30 || this.snakeY <= 0 || this.snakeY > 30) {
        this.gameOver = true
      }

      for (let i = 0; i < this.snakeBody.length; i++) {
        htmlMarkup += `<div class="head" style="grid-area: ${this.snakeBody[i][1]} / ${this.snakeBody[i][0]}"></div>`
        if (i != 0 && this.snakeBody[0][1] === this.snakeBody[i][1] && this.snakeBody[0][0] === this.snakeBody[i][0]) {
          this.gameOver = true
        }
      }

      playBoard.innerHTML= htmlMarkup
    },
    randomFood() {
      this.foodX = Math.floor(Math.random() * 30 ) + 1
      this.foodY = Math.floor(Math.random() * 30 ) + 1
    },
    handleDirections(e) {
      if (e.key === 'ArrowUp' && this.velocityY != 1) {
        this.velocityX = 0
        this.velocityY = -1
      } else if (e.key === 'ArrowDown' && this.velocityY != -1) {
        this.velocityX = 0
        this.velocityY = 1
      } else if (e.key === 'ArrowLeft' && this.velocityX != 1) {
        this.velocityX = -1
        this.velocityY = 0
      } else if (e.key === 'ArrowRight' && this.velocityX != -1) {
        this.velocityX = 1
        this.velocityY = 0
      } else {
        return false
      }
      this.initGame()
    },
    handleGameOver() {
      clearInterval(this.intervalId)
      alert('Game Over !! Press OK to restart ..')
      location.reload()
    },
  },
  // created() {
  //   this.initGame();
  // },
  mounted() {
    this.randomFood()
    this.intervalId = setInterval(() => {
      this.initGame();
    }, 125);
    document.addEventListener('keydown', this.handleDirections)
  },
  beforeDestroy() {
    document.removeEventListener('keydown', this.handleDirections);
  },
}
</script>

<style lang="scss">
.game-wrapper {
  width: 50vmin;
  height: 55vmin;
  background-color: #CAD2C5;
  overflow: hidden;

  .game-details {
    background-color: #354F52;
  }

  .game-play-board {
    display: grid;
    grid-template: repeat(30, 1fr) / repeat(30, 1fr);

    .food {
      background-color: darkred;
    }

    .head {
      background-color: #2F3E46;
    }
  }
}
</style>